import { Button } from "bootstrap"
import { useEffect } from "react"
import React, { useState } from 'react';
import { useParams } from "react-router-dom"
import styled from "styled-components"
import data from "../data/data"
import { Nav, Tab } from "react-bootstrap";
import { useDispatch } from 'react-redux';
import { addItem } from '../store'


function Detail (props) {

  


  let [fade2, setFade2] = useState('')

  let [Delete, setDelete] = useState(1)
  let YelloBtn = styled.button`
  background: yellow;
  padding:10px`

  let[tab, setTab] = useState(0)
  let dispatch = useDispatch()

  useEffect( ()=> {

    setFade2('end')

    let a = setTimeout( ()=> {
      setDelete(0)

    }, 2000)

    let historyGoods = data[id].id //   id
    let gotIt = localStorage.getItem('watched') //  watched .
    
    gotIt = JSON.parse(gotIt) //    parse.
    gotIt.push(historyGoods) //gitIt    id .

    //set =    array 

    gotIt = new Set(gotIt) //   Set .
    gotIt = Array.from(gotIt) //array .

    localStorage.setItem('watched',JSON.stringify(gotIt))


    
    return() => { //unmount .
      clearTimeout(a)
      setFade2('')
      //return useEffect   
      // clean up function 
      //     .
    }    


  },[]) //useEffect  []   . []  ,    .
  // defendency ,  defendency  mount . 
   let { id } = useParams() // url   .
   let num = parseInt(id)+1

return (

<div className={"container start" + fade2}>
  <div>{Delete==1 ? <YelloBtn></YelloBtn>: null}</div>

  <div className="row">
    <div className="col-md-6">
      <img src={'https://codingapple1.github.io/shop/shoes' + num + '.jpg'} width="100%" />

    </div>
        <div className="col-md-6">
      <h4 className="pt-5">{data[id].title}</h4>
      <p>{data[id].content}</p>
      <p>{data[id].price}</p>
      <button className="btn btn-danger" onClick={()=>{
        dispatch(addItem( { id: data[id].id, name: data[id].title }))
        }}></button> 
    </div>
  </div>

  <Nav variant="tabs"  defaultActiveKey="link0"> 
  {/* defaultActiveKey=   */}
    <Nav.Item>
      <Nav.Link eventKey="link0" onClick={() => {setTab(0)}}>0</Nav.Link>
    </Nav.Item>
    <Nav.Item>
      <Nav.Link eventKey="link1" onClick={() => {setTab(1)}}>1</Nav.Link>
    </Nav.Item>
    <Nav.Item>
      <Nav.Link eventKey="link2" onClick={()=> {setTab(2)}}>2</Nav.Link>
    </Nav.Item>
</Nav>
<TabContent tab={tab}></TabContent>

</div> 
)
}

function TabContent({tab}) { // return    .
  
  let [fade,setFade] = useState('')
  useEffect( () => {
    
    setTimeout( ()=>{setFade('end')},1000) // fade end .
    return () => { //cleanupFunction Effect .
      setFade(' ')
    }
  },[tab]) 

if( tab == 0 ) { //if html   .
  return( <div className={'start '+fade}></div>)
} else if( tab== 1 ) {
  return( <div> 1</div>)
} else if( tab==2) {
  return( <div> 2</div>)
}
  
}


export default Detail