
import { createSlice } from '@reduxjs/toolkit';

let user = createSlice( //state  slice .
{
    // name:'state',
    // initialState: ' state '
    name: 'user',
    initialState: { name:'kim', age:10},

    reducers : {
        changeName(state){ // state  state( kim) .
            return { name:'park', age:20}
        },
        plusAge(state, action){ //a state     .
                state.age += action.payload //payload     .
        }
    }

}
)

export default user
